import { Component } from "./react";

const ReactDom = {};

ReactDom.render = (vnode, container) => {
    container.innerHTML = '';
    return render(vnode, container);
}

export default ReactDom;

export function render(vnode, container) {
    return container.appendChild(_render(vnode));
}

// 把对象渲染为dom
function _render(vnode) {
    if (vnode === null || typeof vnode === undefined || typeof vnode === 'boolean') {
        vnode = '';
    }

    if (typeof vnode === 'number') {
        vnode = String(vnode);
    }

    // 当vnode是字符串，渲染结果是一段文本
    if (typeof vnode === 'string') {
        return document.createTextNode(vnode);
    }

    // vnode是函数(普通函数或者类)
    if (typeof vnode.tag === 'function') {
        const component = createComponent(vnode.tag, vnode.attrs);
        setComponentProps(component, vnode.attrs);
        return component.base;
    }

    // vnode是div等
    const dom = document.createElement(vnode.tag);
    if (vnode.attrs) {
        Object.keys(vnode.attrs).forEach(key => {
            setAttribute(dom, key, vnode.attrs[key]);
        });
    }

    vnode.children.forEach(child => {
        render(child, dom);
    });

    return dom;
}

export function setAttribute(dom, key, value) {
    if (key === 'className') {
        key = 'class'
    }

    if (/^on\w+$/.test(key)) {
        key = key.toLowerCase();
        dom[key] = value || '';
    } else if (key === 'style') {
        if (!value || typeof value === 'string') {
            dom.style.cssText = value || '';
        } else if (value && typeof value === 'object') {
            for (let name in value) {
                dom.style[name] = typeof value[name] === 'number' ? value[name] + 'px' : value[name];
            }
        }
    } else {
        if (value) {
            dom.setAttribute(key, value);
        } else {
            dom.removeAttribute(name, value);
        }
    }
}

export function createComponent(component, props, children) {
    let inst;
    if (component.prototype && component.prototype.render) {
        inst = new component(props);
    } else {
        inst = new Component(props);
        inst.constructor = component;
        inst.render = function () {
            return this.constructor(props);
        }
    }
    return inst;
}

function setComponentProps(component, props) {
    // 第一次创建
    if (!component.base) {
        if (component.componentWillMount) component.componentWillMount();
    } else if (component.componentWillReceiveProps) {
        component.componentWillReceiveProps(props);
    }

    renderComponent(component);
}

export function renderComponent(component) {
    let base;

    // shouldComponentUpdate(nextProps, nextState)

    const vnode = component.render.call(component);

    if (component.base && component.componentWillUpdate) component.componentWillUpdate();

    base = _render(vnode);

    if (component.base) {
        component.componentDidUpdate && component.componentDidUpdate();
    } else if (component.componentDidMount) {
        component.componentDidMount();
    }

    if (component.base && component.base.parentNode) {
        component.base.parentNode.replaceChild(base, component.base);
    }

    component.base = base;
    base._component = component;

}



